body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  border-bottom: solid 1px #e4e4e4;
  background-color: #fff;
  opacity: 0.9;
  z-index: 1; }
  .navbar .nav-container {
    position: relative;
    margin: 0 auto;
    height: 60px;
    max-width: 1100px; }
    .navbar .nav-container .logo {
      position: absolute;
      width: 32px;
      height: 32px;
      top: 14px;
      left: 15px; }

.container {
  margin: 0 auto;
  margin-top: 90px;
  border: solid 1px #e4e4e4;
  max-width: 1000px; }
  .container .post-title {
    color: #3B444E;
    font-weight: bold;
    text-decoration: none; }
  .container .post-title:hover {
    color: #666; }

.post-content body > *:first-child {
  margin-top: 0 !important; }

.post-content body > *:last-child {
  margin-bottom: 0 !important; }

.post-content a {
  color: #00bfb6;
  text-decoration: none; }

.post-content a.absent {
  color: #cc0000; }

.post-content a.anchor {
  display: block;
  padding-left: 30px;
  margin-left: -30px;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0; }

.post-content h1, .post-content h2, .post-content h3, .post-content h4, .post-content h5, .post-content h6 {
  margin: 20px 0 10px;
  padding: 0;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  cursor: text;
  position: relative; }

.post-content h2:first-child, .post-content h1:first-child, .post-content h1:first-child + h2, .post-content h3:first-child, .post-content h4:first-child, .post-content h5:first-child, .post-content h6:first-child {
  margin-top: 0;
  padding-top: 0; }

.post-content h1:hover a.anchor, .post-content h2:hover a.anchor, .post-content h3:hover a.anchor, .post-content h4:hover a.anchor, .post-content h5:hover a.anchor, .post-content h6:hover a.anchor {
  text-decoration: none; }

.post-content h1 tt, .post-content h1 code {
  font-size: inherit; }

.post-content h2 tt, .post-content h2 code {
  font-size: inherit; }

.post-content h3 tt, .post-content h3 code {
  font-size: inherit; }

.post-content h4 tt, .post-content h4 code {
  font-size: inherit; }

.post-content h5 tt, .post-content h5 code {
  font-size: inherit; }

.post-content h6 tt, .post-content h6 code {
  font-size: inherit; }

.post-content h1 {
  font-size: 28px;
  color: black; }

.post-content h2 {
  font-size: 24px;
  border-bottom: 1px solid #cccccc;
  color: black; }

.post-content h3 {
  font-size: 18px; }

.post-content h4 {
  font-size: 16px; }

.post-content h5 {
  font-size: 14px; }

.post-content h6 {
  color: #777777;
  font-size: 14px; }

.post-content p, .post-content blockquote, .post-content ul, .post-content ol, .post-content dl, .post-content li, .post-content table, .post-content pre {
  margin: 15px 0; }

.post-content hr {
  border: 0 none;
  color: #cccccc;
  height: 4px;
  padding: 0; }

.post-content body > h2:first-child {
  margin-top: 0;
  padding-top: 0; }

.post-content body > h1:first-child {
  margin-top: 0;
  padding-top: 0; }

.post-content body > h1:first-child + h2 {
  margin-top: 0;
  padding-top: 0; }

.post-content body > h3:first-child, .post-content body > h4:first-child, .post-content body > h5:first-child, .post-content body > h6:first-child {
  margin-top: 0;
  padding-top: 0; }

.post-content a:first-child h1, .post-content a:first-child h2, .post-content a:first-child h3, .post-content a:first-child h4, .post-content a:first-child h5, .post-content a:first-child h6 {
  margin-top: 0;
  padding-top: 0; }

.post-content h1 p, .post-content h2 p, .post-content h3 p, .post-content h4 p, .post-content h5 p, .post-content h6 p {
  margin-top: 0; }

.post-content li p.first {
  display: inline-block; }

.post-content ul, .post-content ol {
  padding-left: 30px; }

.post-content ul :first-child, .post-content ol :first-child {
  margin-top: 0; }

.post-content ul :last-child, .post-content ol :last-child {
  margin-bottom: 0; }

.post-content dl {
  padding: 0; }

.post-content dl dt {
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
  padding: 0;
  margin: 15px 0 5px; }

.post-content dl dt:first-child {
  padding: 0; }

.post-content dl dt > :first-child {
  margin-top: 0; }

.post-content dl dt > :last-child {
  margin-bottom: 0; }

.post-content dl dd {
  margin: 0 0 15px;
  padding: 0 15px; }

.post-content dl dd > :first-child {
  margin-top: 0; }

.post-content dl dd > :last-child {
  margin-bottom: 0; }

.post-content blockquote {
  border-left: 4px solid #dddddd;
  padding: 0 15px;
  color: #777777; }

.post-content blockquote > :first-child {
  margin-top: 0; }

.post-content blockquote > :last-child {
  margin-bottom: 0; }

.post-content table {
  padding: 0; }

.post-content table tr {
  border-top: 1px solid #cccccc;
  background-color: white;
  margin: 0;
  padding: 0; }

.post-content table tr:nth-child(2n) {
  background-color: #f8f8f8; }

.post-content table tr th {
  font-weight: bold;
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 6px 13px; }

.post-content table tr td {
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 6px 13px; }

.post-content table tr th :first-child, .post-content table tr td :first-child {
  margin-top: 0; }

.post-content table tr th :last-child, .post-content table tr td :last-child {
  margin-bottom: 0; }

.post-content img {
  max-width: 100%; }

.post-content span.frame {
  display: block;
  overflow: hidden; }

.post-content span.frame > span {
  border: 1px solid #dddddd;
  display: block;
  float: left;
  overflow: hidden;
  margin: 13px 0 0;
  padding: 7px;
  width: auto; }

.post-content span.frame span img {
  display: block;
  float: left; }

.post-content span.frame span span {
  clear: both;
  color: #333333;
  display: block;
  padding: 5px 0 0; }

.post-content span.align-center {
  display: block;
  overflow: hidden;
  clear: both; }

.post-content span.align-center > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: center; }

.post-content span.align-center span img {
  margin: 0 auto;
  text-align: center; }

.post-content span.align-right {
  display: block;
  overflow: hidden;
  clear: both; }

.post-content span.align-right > span {
  display: block;
  overflow: hidden;
  margin: 13px 0 0;
  text-align: right; }

.post-content span.align-right span img {
  margin: 0;
  text-align: right; }

.post-content span.float-left {
  display: block;
  margin-right: 13px;
  overflow: hidden;
  float: left; }

.post-content span.float-left span {
  margin: 13px 0 0; }

.post-content span.float-right {
  display: block;
  margin-left: 13px;
  overflow: hidden;
  float: right; }

.post-content span.float-right > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: right; }

.post-content code, .post-content tt {
  margin: 0 2px;
  padding: 0 5px;
  white-space: nowrap;
  border: 1px solid #eaeaea;
  background-color: #f8f8f8;
  border-radius: 3px; }

.post-content pre code {
  margin: 0;
  padding: 0;
  white-space: pre;
  border: none;
  background: transparent; }

.post-content pre {
  background-color: #F5F7F9 !important;
  border: 1px solid #efefef !important;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px; }

.post-content pre code, .post-content pre tt {
  background-color: transparent;
  border: none; }

